 * @LastEditors: Please set LastEditors
 * @Description: In User Settings Edit
 * @FilePath: \things_wechat\pages\things\user\demand\car\info\index.vue
-->
<template>
    <view class="order_car_info">
        <!-- tabbar -->
        <cu-custom :refresh="preRrefresh"><block slot="content">车单详情</block></cu-custom>
        <scroll-view class="order_car_info_scroll" :class="(orderInfo.status >= 1 && orderInfo.status < 5) || orderInfo.status === 13 || orderInfo.status === 6 || orderInfo.status === 8 ? 'btn-padding-bottom':''" scroll-y="true">
            <!-- 订单状态 -->
            <view class="flex justify-center align-start order_type_info">
                <view class="type_text">{{ orderInfo.status | demandCarorder_format }}</view>
                <view class="type_tip_text">{{ orderInfo.status | demandCarOrderTip_format }}</view>
            </view>
            <!-- 货物信息/货物选择 -->
            <view class="cu-list menu"  v-if="orderInfo.status === 2 || orderInfo.status === 3">
                <view class="cu-item arrow" @tap="openSelectFreightModal">
                    <view class="content">
                        <text v-if="orderInfo.grabsUserOrder.length > 0" class="text-grey">选择货物</text>
                        <text v-else class="text-grey">暂无人抢单</text>
                    </view>
                </view>
            </view>
            <view class="cu-bar bg-white solid-bottom margin-top" v-if="orderInfo.status > 4">
				<view class="action">
					<text class="cuIcon-title text-orange "></text> 货主信息
				</view>
			</view>
            <view class="things_width_full order_info" v-if="orderInfo.status >= 4">
                <view class="order_single_info">货主：{{orderInfo.grabsUserOrder[0].grabsNickname}}</view>
                <view class="order_single_info">货物名称：{{orderInfo.grabsUserOrder[0].grabsGoodsName}}</view>
                <view class="order_single_info">货物类型：{{orderInfo.grabsUserOrder[0].grabsGoodsName}}</view>
                <view class="order_single_info">重量：{{orderInfo.grabsUserOrder[0].grabsWeight}}</view>
                <view class="order_single_info">装车地点：{{orderInfo.grabsUserOrder[0].grabsLoadingAddress}}</view>
                <view class="order_single_info">装车地点：{{orderInfo.grabsUserOrder[0].grabsUnloadingAddress}}</view>
            </view>
            <view class="cu-bar bg-white solid-bottom margin-top">
				<view class="action">
					<text class="cuIcon-title text-orange "></text> 货车信息
				</view>
			</view>
            <!-- 货车信息 -->
            <view class="things_width_full car_info">
                <!-- 地址 -->
                <view class="flex justify-between align-center address_info">
                    <view class="address_info_left">
                        <view class="text-cut start_place">装车地点：{{orderInfo.loadingAddress}}</view>
                        <view class="text-cut arrive_place">卸车地点：{{orderInfo.unloadingAddress}}</view>
                    </view>
                    <view class="address_info_right" @tap="linkToGps">
                        <image class="gps_icon" src="./../../../../../../static/images/global/gps_icon.png" mode="widthFix" />
                    </view>
                </view>
                <view class="car_basic_info">
                    <view class="order_num">订单编号：{{orderInfo.orderNo}}</view>
                    <view class="things_width_full flex justify-start align-center user_info">
                        <view class="imageBox">
                            <image class="user_cover" :src="orderInfo.avatarUrl" mode="widthFix"/>
                        </view>
                        <view class="name">{{orderInfo.nickname}}</view>
                        <view class="flex justify-start align-center score">
                            <image class="score_icon" src="./../../../../../../static/images/global/score_icon.png" mode="widthFix" />
                            <view class="score_num">{{orderInfo.reputation}}</view>
                            <view class="identity_text">{{orderInfo.UserType}}</view>
                        </view>
                    </view>
                    <view class="things_width_full flex justify-between align-center car_double_info">
                        <view class="car_left_info">运输价格：￥{{orderInfo.totalMoney}}</view>
                        <view class="car_right_info construction_fee">建设费:￥{{orderInfo.build_money}}</view>
                    </view>
                    <view class="car_single_info">车内灌质：{{orderInfo.carFilling}}</view>
                    <view class="car_single_info">车辆类型：{{orderInfo.CarsType}}</view>
                    <view class="car_single_info">车牌号：{{orderInfo.carNumber}}</view>
                    <view class="car_single_info">挂车牌号：{{orderInfo.listingCarNumber}}</view>
                    <view class="car_single_info">罐体立方数：{{orderInfo.cubicNumber}}m³</view>
                    <view class="car_single_info">车辆所属公司：{{orderInfo.carCompany}}</view>
                    <view class="car_single_info">开始时间：{{orderInfo.startTime}}</view>
                    <view class="car_single_info">结束时间：{{orderInfo.endTime}}</view>
                    <view class="car_single_info">下单时间：{{orderInfo.createTime}}</view>
                    <view class="car_single_info">备注：{{orderInfo.releaseRemark}}</view>
                    <!-- 支付收据单据 -->
                    <!-- <view class="pay_msg" v-if="orderInfo.status > 2">
                        <view class="pay_title">支付收据</view>
                        <view class="things_width_full receipt_list">
                            <image class="receipt_item" :src="orderInfo.status" mode="widthFix" />
                        </view>
                    </view> -->
                    <!-- 上传单据按钮 -->
                    <!-- <button class="bg-white cu-btn receipt_btn" v-if="orderInfo.status == 8" @tap="payCarsOrderFun">上传收据</button> -->
                </view>
            </view>
            <!-- 货车上传到达单据 -->
            <view class="cu-bar bg-white solid-bottom margin-top receipt_bar" v-if="orderInfo.status >= 9">
				<view class="action">
					<text class="cuIcon-title text-orange "></text> 上传单据信息
				</view>
			</view>
            <view class="receipt_msg" v-if="orderInfo.status >= 9">
                <view class="car_single_info">损耗：{{orderInfo.loss_weight}}吨</view>
                <view class="things_width_full receipt_list">
                    <image class="receipt_item" :src="orderInfo.transport_pic.pic_path" :data-url="orderInfo.transport_pic.pic_path" mode="widthFix" @tap="previewImage" />
                </view>
            </view>
        </scroll-view>
        <!-- 按钮操作区域 -->
        <view class="things_width_full flex justify-end align-center btn_box" v-if="(orderInfo.status >= 1 && orderInfo.status < 5) || orderInfo.status === 13 || orderInfo.status === 6 || orderInfo.status === 8">
            <!-- 取消订单 -->
            <button class="bg-white cu-btn cancle_btn" v-if="orderInfo.status >= 1 && orderInfo.status < 5" :data-id="orderInfo.orderId" @tap="cancelOrderFun">取消订单</button>
            <!-- 更新订单 -->
            <button class="bg-white cu-btn submit_btn" v-if="orderInfo.status === 13" :data-id="orderInfo.orderId" @tap="linkToUpdate">修改需求</button>
            <!-- 确认运输 -->
            <button class="bg-white cu-btn submit_btn" v-if="orderInfo.status === 6" :data-id="orderInfo.orderId" @tap="confirmTransportFun">开始运输</button>
            <!-- 完成运输 -->
            <button class="bg-white cu-btn submit_btn" v-if="orderInfo.status === 8" :data-id="orderInfo.orderId" @tap="linkToUploadReceipt">完成运输</button>
            <!-- 运输中,确认完成，跳到上传单据 -->
            <!-- <button class="bg-white cu-btn submit_btn" v-if="orderInfo.status === 4" @tap="submitConfirmFun">确认完成</button> -->
            <!-- 已完成，提示平台是否支付车款 -->
            <!-- <button class="bg-white cu-btn assess_btn" v-if="orderInfo.status == 5" @tap="payCarsOrderFun">评价</button> -->
        </view>
        <!-- 选择货物弹框 -->
        <selectFreight @closeSelectFreightModal="closeSelectFreightModal" @selectFreightFun="selectFreightFun" :modalShow="selectFreightModal" :freightList="orderInfo.grabsUserOrder"></selectFreight>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>

<script>
import { getCarOrderDetail_api,cancelOrder_api,dispatchOrder_api,confirmTransactionReleaseCars_api } from '@/api/index.js';
import { appData } from '@/utils/config.js';
import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
import storage from '@/utils/storage.js';
import { identity_format } from '@/utils/filters.js';
import checkForm from '@/utils/checkFormData.js';
import { demandCarorder_format,demandCarOrderTip_format } from '@/utils/filters.js';
import selectFreight from '../../components/selectFreight/selectFreight.vue';
let that = null;
export default {
    components: { selectFreight },
    data() {
        return {
            appBaseData:null,//全局配置信息
            userInfo: {},
            selectFreightModal: false,// 选择货主弹框
            order_id: '',
            orderInfo: {},// 订单详情
            buttonType: false,
            preRrefresh: false,// 判断上一页是否要刷新
        }
    },
    watch: {
        '$store.getters.base_refreshPage': function(val) {
            if (val) {
                // 更新数据
                that.clearPageFun();
                that.base({name:'refreshPage',value:false});
            }
        },
        '$store.getters.base_buttonType': function(val) {
            that.buttonType = val;
        },
    },
    // 过滤器
    filters: {
        // 订单状态
        demandCarorder_format,
        demandCarOrderTip_format
    },
    methods: {
        ...mapMutations([
            'modal',
            'base'
        ]),
        /**清空数据
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        clearPageFun(e) {
            that.getCarOderInfo();
        },
        /**打开选择货物弹框
         * @description: 
         * @param {*} e
         * @return {*}
         */
        openSelectFreightModal(e) {
            if (that.orderInfo.grabsUserOrder.length > 0) {
                that.selectFreightModal = true;
            }
        },
        /**关闭选择货物弹框
         * @description: 
         * @param {*} e
         * @return {*}
         */
        closeSelectFreightModal(e) {
            that.selectFreightModal = false;
        },
        /**选择货物
         * @description: 
         * @param {*} e
         * @return {*}
         */
        selectFreightFun(e) {
            let params = {
                grabs_order_id: e.grabsOrderId,
                type: 30
            };
            dispatchOrder_api(params,that).then(res=>{
                that.modal({name:'popTipModal',value:{type:true,tipTitle:'提示',tipContent:'派单成功'}});
                that.closeSelectFreightModal();
                that.getCarOderInfo();
            }).catch(err=>{
                console.log(err)
            })
        },
        /**跳转到gps
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        linkToGps(e) {
            // uni.navigateTo({
            //     url: `/pages/things/user/order/gps/index`
            // });
        },
        /**取消订单
         * @description: 
         * @param {*} e
         * @return {*}
         */
        cancelOrderFun(e) {
            let params = {
                order_id: e.currentTarget.dataset.id,
                type: that.base_cancelOrderType[1].value,
            };
            that.buttonType = true;
            cancelOrder_api(params,that).then(res=>{
                that.modal({name:'popTipModal',value:{type:true,tipTitle:'提示',tipContent:'取消成功'}});
                that.clearPageFun();
                that.preRrefresh = true;
                that.buttonType = false;
            }).catch(err=>{
                that.buttonType = false;
                console.log(err);
            })
        },
        /**跳转到更新需求
         * @description: 
         * @param {*} e
         * @return {*}
         */
        linkToUpdate(e) {
            that.buttonType = true;
            uni.navigateTo({
                url: `/pages/things/user/demand/car/updateOrder/index?order_id=${that.order_id}`,
                complete: function(res) {
                    that.buttonType = false;
                }
            });
        },
        /**点击开始运输
         * @description: 
         * @param {*} e
         * @return {*}
         */
        confirmTransportFun(e) {
            let params = {
                release_order_id: e.currentTarget.dataset.id,
            };
            confirmTransactionReleaseCars_api(params,that).then(res=>{
                that.modal({name:'popTipModal',value:{type:true,tipTitle:'提示',tipContent:'请开始运输货物'}});
                that.preRrefresh = true;
                that.getCarOderInfo();
            }).catch(err=>{
                console.log(err);
            })
        },
        /**跳转上传收据
         * @description: 
         * @param {*} e
         * @return {*}
         */
        linkToUploadReceipt(e) {
            that.buttonType = true;
            uni.navigateTo({
                url: `/pages/things/user/demand/car/uploadReceipt/index?order_id=${e.currentTarget.dataset.id}`,
                complete: function(res) {
                    that.buttonType = false;
                }
            });
        },
        /**抢车单详情
         * @description: 
         * @param {*} e
         * @return {*}
         */
        getCarOderInfo(e) {
            let params = {
                order_id: that.order_id
            };
            getCarOrderDetail_api(params,that).then(res=>{
                that.orderInfo = res;
            }).catch(err=>{
                console.log(err)
            })
        },
        previewImage(e) {
            console.log(e);
            // 预览图片
            uni.previewImage({
                urls: [e.currentTarget.dataset.url],
                current: e.currentTarget.dataset.url
            });
        }
    },
    // 页面生命周期
    onLoad: function (options) {
        that = this;
        if (!options.order_id || options.order_id == ''|| options.order_id == 'undefined') {
            that.modal({name:'popLinkDialog',value:{type:true,tipTitle:'提示',tipContent:'该订单不存在',linkUrl:1}});
            return false;
        }
        that.order_id = options.order_id;
        that.getCarOderInfo();
    }
}
</script>

<style lang="scss" scoped>
    @import "./index";
</style>
